<template>
  <div>
    <div v-for="(prompt, i) in prompt.items" :key="i" :is="getComponent(prompt)" :prompt="prompt" :responses="responses" @change="promptFilled" class="m2-bottom"></div>
    <button class="button alt h-fill m5-top" @click="done">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </div>
</template>

<script>
import promptComponents from '@/components/prompts/prompt-components'

export default {
  name: 'GroupPrompt',
  data () {
    return {
      formResponses: {}
    }
  },
  props: ['prompt', 'responses'],
  methods: {
    getComponent (prompt) {
      return promptComponents[prompt.type]
    },
    promptFilled (responseValues) {
      for (const key in responseValues) {
        this.formResponses[key] = responseValues[key]
      }
    },
    done () {
      this.$emit('done', this.formResponses)
      this.$children.forEach(c => {
        if (c.clear) {
          c.clear()
        }
      })
    }
  }
}
</script>

