<template>
  <div class="text-center">
    <button v-for="(option, i) in prompt.options" :key="i" class="button border-clear contrast m2-top m1-right" :class="[responseValues[option.key] === 'Yes' ? 'bg-primary' : 'option']" @click="selected(option)">{{option.prompt || option.key}}</button>
    <button class="button alt h-fill m5-top" @click="done">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </div>
</template>

<script>
export default {
  name: 'OptionsPrompt',
  data () {
    return {
      responseValues: {}
    }
  },
  props: ['prompt'],
  methods: {
    selected (option) {
      this.$set(this.responseValues, option.key, this.responseValues[option.key] === 'Yes' ? '' : 'Yes')
    },
    classes (option) {
      return this.responseValues[option.key] === 'Yes' ? ['bg-primary'] : []
    },
    done () {
      this.$emit('done', this.responseValues)
    }
  }
}
</script>

<style lang="scss" scoped>
.button.option {
  background-color: transparent;
  border: solid 1px #aaa !important;
}
.border-clear {
  border: solid 1px transparent;
}
</style>
