<template>
  <div class="flex-column p2">
    <button v-for="(option, i) in prompt.options" :key="i" class="button alt contrast p3 shadow m2-bottom" @click="done(option)">{{option.prompt}}</button>
  </div>
</template>

<script>
export default {
  name: 'RadioPrompt',
  props: ['prompt'],
  methods: {
    done (option) {
      let responseValues = {}
      this.prompt.options.forEach(o => (responseValues[o.key] = ''))
      responseValues[option.key] = 'Yes'
      this.$emit('done', responseValues)
    }
  }
}
</script>

