<template>
  <div class="flex-column p4">
    <button class="button alt p3 shadow m4-bottom" @click="done('Yes')">YES</button>
    <button class="button alt p3 shadow" @click="done('')">NO</button>
  </div>
</template>

<script>
export default {
  name: 'BoolPrompt',
  props: ['prompt', 'responses'],
  methods: {
    done (value) {
      this.$emit('done', { [this.prompt.key]: value })
    }
  }
}
</script>

