<template>
  <form @submit.prevent="done">
    <input type="text" :placeholder="placeholder" v-model="fieldValue" autofocus="true" class="input input-tertiary" />
    <button v-if="showDoneButton" class="button alt h-fill m5-top" :disabled="!fieldValue">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </form>
</template>

<script>
export default {
  name: 'SplitTextPrompt',
  data () {
    return {
      fieldValue: undefined
    }
  },
  props: ['prompt'],
  computed: {
    placeholder () {
      return this.prompt.placeholder || 'answer here'
    },
    showDoneButton () {
      return !['GroupPrompt', 'OptionsPrompt'].includes(this.$parent.$options.name)
    }
  },
  watch: {
    fieldValue () {
      this.$emit('change', { [this.prompt.key]: this.fieldValue })
    }
  },
  methods: {
    done () {
      let responseValues = {}
      let startIndex = 0
      const self = this
      this.prompt.keys.forEach(k => {
        responseValues[k.key] = self.fieldValue.slice(startIndex, k.maxLength || 1000000)
        startIndex += k.maxLength
      })
      console.log(responseValues)

      this.$emit('done', responseValues)
      this.fieldValue = undefined
    }
  }
}
</script>
